// PAGINATION COMPONENT

.pagination {
    margin-top: 60px;
    &_control {
        border-radius: 50%;
        width: 30px;
        height: 30px;
        font-size: 16px;
        line-height: 22px;
        .icon {
            transition: all .3s ease-in-out;
        }
        &--prev {
            &:hover:not([aria-disabled="true"]),
            &:focus:not([aria-disabled="true"]) {
                .icon {
                    margin-right: 5px;
                }
            }
        }
        &[aria-disabled="true"] {
            border: 1px solid #C8C8C8;
            color: #C8C8C8;
            cursor: default;
        }
        &--next {
            border: 1px solid $primary;
            &:hover:not([aria-disabled="true"]),
            &:focus:not([aria-disabled="true"]) {
                .icon {
                    margin-left: 5px;
                }
            }
        }
    }
    &_list {
        margin: 0 30px;
        &-item {
            margin-right: 30px;
            &:last-of-type {
                margin-right: 0;
            }
            &_link {
                color: $gray;
                transition: all .3s ease-in-out;
                &:hover,
                &:focus,
                &--current {
                    color: $blackalt;
                    font-weight: 500;
                }
            }
        }
    }
}